import backgroundHttpBridge from '@/utils/backgroundHttpBridge.js';

import {BASE_API_URL} from '@/config.js';

class StylesApi {
  async search (q) {
    let results = [];
    try {
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/styles/search?q=' + q,
        method: 'GET'
      });

      results = response.data.styles;
    } catch (error) {

    }

    return results;
  }
}

const singleton = new StylesApi();
export default singleton;



