<template>
  <div style="background:#fff; bottom:0; left:0; position:absolute; right:0; top:0; z-index:10;">
    <header class="mb2">
      <div class="absolute" style="left:0; top:0;">
        <mu-icon-button @click="close" icon="arrow_back"/>
      </div>
      <div class="f4 mh5 pv2 tc">
        Select your style
      </div>
    </header>
    <div class="absolute flex flex-column mh3" style="bottom:10px; left:0; top:46px; right:0;">
      <div class="f5 mb3 tc">
        <div class="mb2">Current style</div>
        <div class="fw5">{{currentStyle.name}}</div>
      </div>
      <div ref="styleSearchBox" class="search-box">
        <input v-model="searchTerm" @keyup="doSearch()" type="text" placeholder="Search?" />
        <i class="material-icons">search</i>
      </div>
      <ul v-if="!isSearching" class="search-results" style="flex-grow:1;">
          <li v-for="(style, index) in searchResults" :key="index">
            <button @click="selectStyle(style)">
              <span><span v-if="style.isPopular" class="popular">Popular</span>{{style.name}}</span>
            </button>
          </li>
      </ul>

      <Spinner v-if="isSearching"/>
    </div>
  </div>
</template>

<script>
import _ from 'lodash';

import Spinner from '@/components/Spinner';

import StylesApi from '@/services/stylesApi.js';

export default {
  name: 'StyleSelector',
  props: ['currentStyle'],
  components: {
    Spinner
  },
  data () {
    return {
      searchTerm: '',
      isSearching: false,
      searchResults: []
    }
  },
  methods: {
    close () {
      this.$emit('close')
    },
    doSearch: _.debounce(async function () {
      this.isSearching = true;
      this.searchResults = await StylesApi.search(this.searchTerm);
      this.isSearching = false;
    }, 200),
    async selectStyle (style) {
      this.$emit('changeStyle', style)
    }
  },
  mounted () {
    this.doSearch('');
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../variables.scss';

  .search-box { margin-bottom:20px; position:relative;
    input[type=text] { border:1px solid #d7dbe0; font-size:14px; padding:12px 12px 12px 50px; width:100%;
      &:focus { border-color:$secondary-color; }
    }
    i { color:#7e8994; left:16px; position:absolute; top:10px; }
  }

  .search-results { overflow-y: scroll;
    button { color:#7e8994; padding: 12px 20px; text-align:left; text-transform:none; width:100%;
      &:hover { background:#f6f8fa; }
      .popular { color:#f9a602; font-size:13px; font-weight:500; margin-right:10px; text-transform:uppercase; }
    }
  }
</style>



