<template>
  <div id="app">
    <Main/>
  </div>
</template>

<script>
import Main from '@/components/Main';

export default {
  name: 'App',
  components: {
    Main
  }
}
</script>

<style lang="scss">
@import 'variables.scss';

html { overflow:hidden; }
body { background:$ui-grey; height:600px; font-family: 'Roboto', Helvetica, Arial, sans-serif; overflow:hidden; width:340px; }
h1, h2, h3, h4, h5, h6 { font-family:'Google Sans'; font-weight:500; }
a, article, body, code, dd, div, dl, dt, fieldset, footer, form, h1, h2, h3, h4, h5, h6, header, html, input[type=email], input[type=number], input[type=password], input[type=tel], input[type=text], input[type=url], legend, li, main, ol, p, pre, section, table, td, textarea, th, tr, ul { box-sizing: border-box; }
ul { list-style-type: none; margin:0; padding:0; }
button { background:none; border:none; color:inherit; cursor: pointer; font-family: 'Roboto', sans-serif; text-transform:uppercase; outline:none;
  span { align-items:center; display:flex; }
  i { margin-right:0.2em; }
}
input, textarea { border:none; font-family: 'Roboto', sans-serif; outline:none;
  &:focus { border-color:$secondary-color !important; }
}
a { text-decoration:none; }

::-webkit-scrollbar { width: 10px; }
::-webkit-scrollbar-thumb { border-radius: 10px; background-color: rgba(0,0,0,0.5); }

.white { color:#ffffff; }
.primary-color {color:$primary-color; }
.secondary-color { color:$secondary-color; }
.red { color:#f44336; }
.grey { color:#d6d6d6; }

.bg-orange { background:$primary-color; }
.bg-blue { background:$secondary-color; }
.bg-red { background:#f44336; }
.bg-grey { background:#d6d6d6; }

.b-orange { border-color:$primary-color; }
.b-blue { border-color:$secondary-color; }
.b-red {border-color:#f44336; }

.mu-flat-button[disabled] { filter:grayscale(100%); }
.mu-flat-button-secondary .mu-flat-button-label { color:$secondary-color; }
.mu-raised-button-primary { background-color:$primary-color; }
.mu-raised-button-secondary { background-color: $secondary-color; }
.mu-raised-button-label { font-weight:500; }
.mu-tooltip { pointer-events: none; }

.fade-forward-enter-active, .fade-forward-leave-active { transition: all .5s }
.fade-forward-enter, .fade-forward-leave-to {  opacity: 0 }
.fade-forward-enter {  transform: translate3d(50px,0,0) }
.fade-forward-leave-to {  transform: translate3d(-50px,0,0) }

.fade-up-enter-active, .fade-up-leave-active { transition: all .2s }
.fade-up-enter, .fade-up-leave-to {  opacity: 0 }
.fade-up-enter {  transform: translate3d(0,50px,0) }
.fade-up-leave-to {  transform: translate3d(0,50px,0) }

.fade-down-enter-active, .fade-down-leave-active { transition: all .2s }
.fade-down-enter, .fade-down-leave-to {  opacity: 0 }
.fade-down-enter {  transform: translate3d(0,-20px,0) }
.fade-down-leave-to {  transform: translate3d(0,-20px,0) }

@keyframes heartbeat
{
  0%
  {
    transform: scale( .75 );
  }
  20%
  {
    transform: scale( 1 );
  }
  40%
  {
    transform: scale( .75 );
  }
  60%
  {
    transform: scale( 1 );
  }
  80%
  {
    transform: scale( .75 );
  }
  100%
  {
    transform: scale( .75 );
  }
}

</style>



