import backgroundHttpBridge from '@/utils/backgroundHttpBridge.js';

import {BASE_API_URL} from '@/config.js';

class AutociteApi {
  async autociteHtml (html, url, styleId) {
    try {
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/autocite/html',
        method: 'POST',
        data: {
          html: html,
          url: url,
          styleId: styleId
        }
      });
      return response.data.result;
    } catch (error) {
    }
  }

  async autociteUrl (url, styleId) {
    try {
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/autocite/url',
        method: 'POST',
        data: {
          url: url,
          styleId: styleId
        }
      });

      return response.data.result;
    } catch (error) {
    }
  }

  async autocitePdf (pdfBlob, url, styleId) {
    try {
      const formData = new FormData();
      formData.append('pdf', pdfBlob);
      formData.append('url', url);
      formData.append('styleId', styleId);
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/autocite/pdf',
        method: 'POST',
        data: formData,
        headers: {
          'content-type': 'multipart/form-data'
        }
      });

      return response.data.result;
    } catch (error) {
    }
  }

  async autociteMetadata (metadata, sourceId, styleId) {
    try {
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/autocite/reference',
        method: 'POST',
        data: {
          metadata: metadata,
          sourceId: sourceId,
          styleId: styleId
        }
      });

      return response.data.result;
    } catch (error) {
    }
  }
}

const singleton = new AutociteApi();
export default singleton;



