<template>
  <div class="name">
    <div class="label">
      <button @click="dropdownIsOpen = true" ref="dropdownTrigger">
        <span>
          {{labelToDisplay}}  <i class="material-icons">arrow_drop_down</i>
        </span>
      </button>
      <mu-popover :trigger="dropdownTrigger" :open="dropdownIsOpen" @close="dropdownIsOpen = false">
        <mu-menu>
          <mu-menu-item :title="label" @click="useLiteralName(false)"/>
          <mu-menu-item title="Organization" @click="useLiteralName(true)"/>
        </mu-menu>
      </mu-popover>
    </div>
    <div class="input">
      <input v-if="!literalNameIsSelected" type="text" v-model="name.given" placeholder="First name(s)" />
      <input v-if="!literalNameIsSelected" type="text" v-model="name.family" placeholder="Last name"/>
      <input v-if="literalNameIsSelected" type="text" v-model="name.literal" class="expand" placeholder="Company or organization name"/>
      <button class="red" style="margin-right:-8px;" title="Remove person" @click="$emit('onDelete')"><i class="material-icons mr0">remove_circle_outline</i></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ReferenceFormName',
  props: ['label', 'name'],
  data () {
    return {
      dropdownIsOpen: false,
      literalNameIsSelected: false,
      dropdownTrigger: null
    }
  },
  computed: {
    labelToDisplay () {
      return this.literalNameIsSelected ? 'Organization' : this.label
    }
  },
  methods: {
    useLiteralName (useLiteralName) {
      this.literalNameIsSelected = useLiteralName;
      if (useLiteralName) {
        this.name.given = null;
        this.name.family = null;
      } else {
        this.name.literal = null;
      }

      this.dropdownIsOpen = false;
    }
  },
  beforeMount () {
    if (this.name.literal) {
      this.literalNameIsSelected = true;
    }
  },
  mounted () {
    this.dropdownTrigger = this.$refs.dropdownTrigger
  }
}
</script>

<style lang="scss">
  .name { margin-bottom:8px; }
  .label { align-items:center; display:flex; font-size:12px; font-weight:700; padding-right:18px; position:relative; text-align:right; text-transform:uppercase;
    button { padding:0; }
    i { margin-right:-8px; }
  }
  .dropdown { background:#ffffff; border:1px solid #d7dbe0; left:0; position:absolute; top:0; width:140px; z-index:5;
    button { justify-content:center; padding: 6px 2px; width:100%;
      &:first-child { border-bottom: 1px dotted #d7dbe0; }
      &:hover { background: darken(#ffffff, 2%);}
      &.selected { background:#f4f6f8;
        &:hover { background: darken(#f4f6f8, 2%);}
      }
    }
  }
  input { display:flex; width:50%;
    &.expand { width:100%; }
  }
</style>



