import backgroundHttpBridge from '@/utils/backgroundHttpBridge.js';

import {BASE_API_URL} from '@/config.js';

class ImportApi {
  async stashReference (reference) {
    try {
      const response = await backgroundHttpBridge({
        url: BASE_API_URL + '/import/stash/json',
        method: 'POST',
        data: {
          importStr: JSON.stringify([{sourceId: reference.sourceId, urlToImage: reference.urlToImage, metadata: reference.metadata}])
        }
      });

      return response.data.token;
    } catch (error) {
    }
  }
}

const singleton = new ImportApi();
export default singleton;



